.qpeTabContainer {
  display: flex;
  border-bottom: 1px solid var(--primary);
  width: 100%;
  overflow: hidden;
  height: 48px;
  position: relative;

  .qpeTabOption {
    font-size: 14px;
    display: flex;
    line-height: 20px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    flex-grow: 1;
    font-weight: bold;
    position: relative;
    transition: background-color 0.2s ease;
    text-align: center;
    color: var(--dim-text);
  }

  .qpeSelectedTab{
    color: var(--dark-text);
  }

  .animatedBorder {
    position: absolute;
    bottom: 0;
    height: 2px;
    background-color: var(--primary);
    transition: transform 0.3s ease, width 0.3s ease;
  }
}
