.qpeInnerContainer {
  .qpeAISController {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0 18px 0;

    & p {
      font-weight: bold;
      font-size: 16px;
    }

    .qpeAISContainer {
      bottom: 16px;
      position: absolute;
      font-weight: bold;
    }
  }
  overflow-y: auto;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  position: absolute;

  &.qpeBorder-None {
    border: none;
  }

  .qpeSoapHeader {
    display: flex;
    justify-content: space-between;

    .qpeEditorButton {
      display: flex;
      align-items: center;
      justify-content: center;
      background: none;
      cursor: pointer;
      border: none;
    }
  }

  .qpeListItemContainer {
    display: flex;
    gap: 10px;
    flex-direction: column;
    padding: 0 15px;
    margin: 10px 0;

    &.editMode {
      border-radius: 12px;
      padding: 10px 20px;
    }
    ul {
      display: flex;
      flex-direction: column;
      gap: 9px;
      padding: 0 4px;

      .qpeListItem {
        list-style: disc;
        text-transform: capitalize;

        input {
          border: none;
          outline: none;
          font-size: 16px;
          width: 100%;
        }

        textarea {
          height: auto;
          border: none;
          outline: none;
        }
      }
    }
  }

  .qpeEditableText {
    width: 100%;
    height: 100%;
    font-size: 16px;
    border: none;
    resize: none;
    outline: none;
    text-align: justify;
  }

  .qpeSessionData {
    display: flex;
    align-items: center;
    flex: 1 1;
    width: 100%;
    height: 100%;
    background: var(--surface-container);
    flex-direction: column;
    overflow-y: auto;
    border-radius: 8px;
    position: absolute;
    justify-content: space-between;

    .qpeElevatedDiv {
      padding: 16px 0;
      z-index: 2;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;

      & #qpeVideoCall {
        background: var(--error);
        height: 100%;
        width: 100%;
      }
    }

    &.qpeBorder-None {
      border: none;
    }

    .qpeTimerContainer {
      background: var(--surface);
      display: flex;
      align-items: center;
      color: var(--white);
      padding: 6px 16px;
      border-radius: 8px;
      color: var(--dark-text);
      gap: 8px;
      margin: 0 auto;
      & .status {
        color: var(--error);
      }
      & span {
        text-transform: capitalize;
      }
    }
  }

  .qpeMajorTextContainer {
    flex: 1 1;
    overflow-y: auto;
    border-radius: 8px;

    .qpeTextContainer {
      text-align: justify;
      display: flex;
      flex-direction: column;
      line-height: 24px;
      width: 100%;
      height: 100%;
      overflow: auto;
      padding-right: 10px;
      border-radius: 8px;

      .qpeEditableText {
        width: 100%;
        outline: none;
        flex: 1 1;
        font-family: "Arial", sans-serif;
        font-size: 16px;
        font-weight: normal;
        color: #333;
        line-height: 1.5;
        padding: 0 10px 0 0;
        white-space: pre-wrap;
      }

      .qpeNoData {
        flex-grow: 1;
        align-items: center;
        display: flex;
        justify-content: center;
      }

      .qpeSection {
        padding: 0;
      }
      .qpeTextArea {
        width: 100%;
        flex: 1 1;
        border-radius: 5px;
        border: none;
        padding: 10px;
        font-size: 16px;
        font-family: inherit;
        resize: none;
        outline: none;
        line-height: 24px;
        font-weight: 400;
        letter-spacing: 0.5px;

        &.custom-scrollbar {
          overflow-y: auto;
        }

        &:disabled {
          color: var(--dark-text);
          cursor: not-allowed;
        }
      }
    }

    .qpeEditButton {
      position: absolute;
      top: 5px;
      right: 10px;
      padding: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: none;
      border-radius: 50%;
      border: none;

      &:hover {
        background: var(--surface);
      }
    }

    .qpeSearchContainerMain {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 0 20px 0;

      .qpeQuestionnaireSearchContainer {
        display: flex;
        align-items: center;
        background: var(--surface-container-high);
        padding: 12px 19px;
        max-width: 360px;
        border-radius: 28px;
        gap: 4px;
        flex-grow: 1;

        .qpeQuestionnaireSearchInput {
          flex: 1;
          border: none;
          outline: none;
          background-color: transparent;
          font-size: 16px;
          border-radius: 28px;
          padding: 0 4px;
        }

        .qpeQuestionnaireSearchIcon {
          background-color: transparent;
          border: none;
          outline: none;
          cursor: pointer;
          width: 24px;
          height: 24px;
        }
      }
    }
  }
}
