* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --primary: #2b6a46;
  --surface: #f6fbf4;
  --dark-text: #181d19;
  --dim-text: #40484c;
  --error: #df3435;
  --primary-container: #002110;
  --surface-container: #edf2eb;
  --white: #ffffff;
  --outline: #c0c8cd;
  --surface-container-high: #e5eae3;
  --color-surface-high: #e3eae6;
}

.qpeLoader {
  width: 25px;
  height: 24px;
  border-top: 2px solid var(--white);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}
.mainContainer {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  background: var(--white);
}

.custom-scrollbar::-webkit-scrollbar {
  width: 4px;
  background-color: transparent;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background-color: #6f7976;
  border-radius: 4px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background-color: #f1f1f1;
  border-radius: 4px;
}

.walkinButton:hover {
  cursor: pointer;
}

.initButton {
  position: fixed;
  right: 85px;
  display: flex;
  align-items: center;
  bottom: 30px;
  background-color: var(--color-primary);
  color: var(--white);
  padding: 10px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.react-ext-container {
  animation: slideUp 0.6s cubic-bezier(0.25, 0.8, 0.25, 1) forwards;
  background-color: var(--white);
  border: 1px solid var(--border-color);
  border-radius: 12px 0 0 0;
  bottom: 0;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  max-height: 640px;
  height: 100%;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  position: fixed;
  right: 0;
  max-width: 940px;
  width: 100%;
  z-index: 999;
  transform: scale(0.95);
  opacity: 0;
  font-family: "Roboto", sans-serif;
}

@keyframes slideUp {
  0% {
    bottom: -50%;
    transform: scale(0.95);
    opacity: 0;
  }

  60% {
    bottom: 3%;
    transform: scale(1.02);
    opacity: 1;
  }

  100% {
    bottom: 0%;
    transform: scale(1);
    opacity: 1;
  }
}
.hidden {
  display: none; /* Hides the element */
  /* Alternatively, you can manage z-index if needed */
  z-index: -1;
  /* pointer-events: none; */
  /* opacity: 0; */
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0.8;
  }
  50% {
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0.8;
  }
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.qpe-toast {
  animation: fadeInOut 0.5s ease-in-out;
}

.hide {
  visibility: hidden;
}

.smLoader {
  width: 25px;
  height: 25px;
  border-top: 2px solid var(--dark-text);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}
