.authForm {
    background: #fff;
    border-radius: 8px;
    padding: 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: auto;
    max-width: 320px;
    height: 100%;
    justify-content: center;

    .authTitle {
        font-size: 24px;
        color: #232f47;
        font-weight: 400;
        text-align: center;
        margin-bottom: 10px;
    }

    .formGroup {
        display: flex;
        flex-direction: column;
        gap: 8px;
        margin: 0 auto;
        width: 100%;

        .label {
            font-size: 0.9rem;
            color: #232f47;
        }

        .input {
            padding: 10px;
            font-size: 1rem;
            border: 1px solid #c3cbd0;
            border-radius: 4px;
            outline: none;
            width: 100%;

            &:focus {
                border-color: #66a8cf;
            }

            &::placeholder {
                color: #a6b1bb;
            }
        }

        .passwordField {
            display: flex;
            align-items: center;
            width: 100%;
            position: relative;

            .togglePassword {
                background: none;
                border: none;
                font-size: 1.2rem;
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-left: 8px;
                right: 0;
                position: absolute;

            }
        }
    }

    .forgotPassword {
        color: #66a8cf;
        font-size: 0.85rem;
        text-align: right;
    }

    .error {
        color: #e74c3c;
        font-size: 0.875rem;
        text-align: center;
    }

    .submitButton {
        padding: 10px 24px;
        font-size: 1rem;
        background-color: #3d77c5;
        color: #fff;
        border: none;
        border-radius: 24px;
        cursor: pointer;
        height: 40px;
        transition: background-color 0.3s ease;
        margin: 0 auto;
        width: 100%;

        &:hover {
            background-color: darken(#3d77c5, 5%);
        }

        &:disabled {
            background-color: #ccc;
            cursor: not-allowed;
        }
    }

    .signUpPrompt {
        font-size: 0.85rem;
        text-align: center;
        color: #232f47;

        .signUpLink {
            color: #3d77c5;
            font-weight: bold;
            text-decoration: none;

            &:hover {
                text-decoration: underline;
            }
        }
    }
}