.qpeAccordionContainer {
  border-radius: 20px;
  overflow: hidden;
  border: 1px solid var(--outline);

  .qpeAccordionItem {
    background: var(--white);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: all 0.3s ease;

    &.active {
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    }

    &:first-child {
      border-bottom: 1px solid var(--outline);
    }
  }
}

.qpeAccordionHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;

  &.Hactive {
    background-color: var(--surface);
  }

  &:hover {
    background-color: var(--surface);
  }

  .qpeAccordionIcon {
    font-size: 20px;
    transition: transform 0.3s ease;
  }
}

.qpeAccordionContent {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out;

  &.active {
    max-height: 250px;
    overflow: auto;
  }

  .qpeQuestionContainer {
    height: auto;
    padding: 15px;
    font-size: 16px;
    color: #333;
  }
}

.qpeQuestionnaireItem {
  display: flex;
  flex-direction: column;
  padding: 8px 16px;

  .qpeQuestionnaireLabel {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.5px;
    text-align: left;
    color: var(--dark-text);
  }

  .qpeQuestionnaireAnswer {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.25px;
    text-align: left;
    color: var(--dim-text);
  }

  .qpeQuestionnaireList {
    list-style-type: disc;
    margin: 0;
    padding-left: 20px;
    text-transform: capitalize;
  }

  .qpeQuestionnaireListItem {
    font-size: 14px;
    margin: 4px 0;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.25px;
    text-align: left;
    color: var(--dim-text);
  }
}
