@use "sass:color";

.qpeIndexMainContainer {
  flex: 1 1;
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;

  .qpeSub-container {
    flex: 1 1;
    display: flex;
    flex-direction: column;

    .qpeTop-container {
      max-height: 20dvh;
      gap: 16px;
      display: flex;
      justify-content: center;
      flex-direction: column;
    }

    .qpeOuterContainer {
      flex: 1 1;
      display: flex;
      flex-direction: column;
      position: relative;
      padding: 20px;

      .qpeContextContainer {
        height: auto;
        flex-direction: column;
        display: flex;
        position: relative;
        flex: 1 1;
        justify-content: space-between;
        border-radius: 8px;
      }
    }
  }
}
