.soapController {
  padding: 0 0 20px 0;
  align-items: center;
  display: flex;
  justify-content: space-between;
  gap: 20px;

  .tempplateHeader {
    flex-shrink: 0;
    display: flex;
    flex: 1 1;

    & p {
      color: var(--dark-text);
      font-size: 16px;
      line-height: 24px;
      font-weight: bold;
    }
  }
}
.qpeResponseCount {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 143px;

  & button {
    padding: 12px 10px;
    background: none;
    border: none;
    cursor: pointer;
    color: var(--outline);
  }
  & p {
    color: var(--dark-text);
    font-weight: bold;
    line-height: 24px;
    font-size: 16px;
  }
}
.soapGrid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  flex: 1 1;
}

.soapBox {
  position: relative;
  border: 1px solid var(--outline);
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  width: 48%;
  flex-grow: 1;
  flex-shrink: 0;
  transition: 0.2s ease all;

  &:focus-within {
    border: 3px solid var(--primary);
  }

  &:hover {
    border: 2px solid var(--dark-text);
  }
}

.soapBoxHeader {
  position: absolute;
  top: -10px;
  left: 16px;
  background: var(--white);
  padding: 0 8px;

  font-size: 12px;
  color: var(--dark-text);
  line-height: 16px;
  z-index: 2;
}

.soapBoxContent {
  flex-grow: 1;
  background: var(--white);
  border: none;
  border-radius: 4px;
  padding: 8px 0 0 16px;
  font-size: 14px;
  font-family: inherit;
  color: var(--dark-text);
  resize: none;
  overflow-y: auto;
  line-height: 1.5;

  &:focus {
    outline: none; // Remove default outline
  }
}
