.qpeIndex-container {
  padding: 10px;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-items: center;
  color: var(--dark-text);
  text-align: center;
  justify-content: center;

  .qpeIndex-heading {
    font-size: 2rem;
    margin-bottom: 2rem;
    color: var(--dark-text);
  }

  .qpeButton-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .qpeIndex-button {
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    color: var(--dark-text);
    background-color: var(--surface);
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;

    &:focus {
      outline: none;
      box-shadow: 0 0 0 3px rgba(102, 168, 207, 0.5);
    }
  }
}
