.qpeProfileContainerMain {
  width: 100%;
  display: flex;
  justify-content: space-around;
  gap: 20px;
  max-height: 60%;
  max-width: 95%;
  flex: 1 1;

  .qpeProfileContainer {
    justify-content: center;
    display: flex;
    border-radius: 20px;
    align-items: center;
    max-width: 90%;
    flex: 1 1;
    flex-direction: column;
    justify-content: space-around;

    

    &.small {
      .qpeProfile {
        width: 24px;
        height: 24px;
        padding: 20px;

        & p {
          font-size: 14px;
        }
      }
    }

    &.medium {
      .qpeProfile {
        width: 60px;
        height: 60px;
        padding: 0px;
        & p {
          font-size: 20px;
        }
      }
    }
  }
}


.qpeProfile {
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background: var(--surface-container-high);

  & p {
    color: var(--text-color);
    font-size: 35px;
  }
}