.qpeHeader {
  position: static;
  color: var(--dark-text-color);
  background-color: var(--surface);
  display: flex;
  padding: 10px;
  height: 90px;

  .qpeSubHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    .qpeHeaderLeft {
      display: flex;
      align-content: center;
      gap: 8px;
    }
    .qpeScibeButton {
      cursor: pointer;
      background: none;
      border: none;
    }

    .qpeHeaderH1 {
      margin: 0;
      line-height: 28px;
      font-size: 22px;
      font-weight: bold;
    }

    .qpeHeaderRight {
      display: flex;
      gap: 8px;
      align-items: center;
      .qpeCloseButton {
        display: flex;
        align-items: center;
        background: none;
        border: none;
        cursor: pointer;
      }
    }
  }
}
