.qpeVideoCallScreenContainer {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  width: 100%;
}

.qpeVideoContainer {
  position: absolute;
  width: 100%;
  height: 100%;

  .local-user-placeholder {
    position: absolute;
    z-index: 2;
    padding: 10px;
    flex-direction: column;
    width: 220px;
    border-radius: 10px;
    height: 120px;
    background: var(--surface);
    align-items: center;
    display: flex;
    gap: 10px;
    justify-content: center;
    transition: 0.3s ease all;
    border: 1px solid var(--outline);
  }
}

.iconContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.remote-user-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s ease all;
  flex-direction: column;
  background: var(--surface-container);

  .remote-place-holder-sub {
    display: flex;
    flex: 1 1;
    max-height: 60%;
    padding: 20px 0;
    justify-content: space-around;
    flex-direction: column;
  }
}
.qpeVideoCallScreen {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.remote-video {
  width: 100%;
  height: 100%;
  display: flex;
  object-fit: cover;
  overflow: hidden;
  position: absolute;
}
