.qpe-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  background-color: rgba(0, 33, 16, 0.7);
  .qpe-modal-container {
    background: var(--white);
    border-radius: 24px;
    width: 400px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    text-align: left;
    font-family: Arial, sans-serif;
    overflow: hidden;
    animation: slideFromTopSmooth 0.3s cubic-bezier(0.25, 1, 0.5, 1);

    .qpe-modal-header {
      padding: 24px;
      background: var(--surface-container-high);

      h2 {
        color: var(--dark-text);
        font-size: 24px;
        font-weight: 400;
        font-family: "Mulish";
        line-height: 32px;
      }

      .qpe-modal-sub-header {
        display: flex;
        flex-direction: column;
        gap: 16px;
        p {
          font-size: 14px;
        }
      }
    }

    .qpe-modal-body {
      display: flex;
      flex-direction: column;

      .qpe-modal-row {
        padding: 4px 24px;
        line-height: 24px;
        .qpe-modal-info {
          &:nth-child(1) {
            font-size: 16px;
          }

          &:nth-child(2) {
            font-size: 14px;
            color: #40484c;
            font-weight: 400;
          }
        }
      }

      .device-settings {
        display: flex;
        flex-direction: column;
        justify-content: center;

        .device-setting {
          display: flex;
          padding: 16px 24px;
          gap: 16px;
          align-items: center;
          position: relative;

          label {
            top: 7px;
            padding: 0 4px;
            left: 78px;
            background: var(--white);
            position: absolute;
            font-size: 12px;
            font-weight: 400;
            color: #40484c;
          }

          .custom-select-container {
            width: 100%;
          }
          select {
            width: 100%;
            font-size: 14px;
            border: 1px solid #ccc;
            border-radius: 4px;
            background-color: #fff;
            color: #333;
            outline: none;
            cursor: pointer;
            transition: border-color 0.2s ease-in-out;
            padding: 8px 16px;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;

            &:focus {
              border-color: var(--primary);
            }

            option {
              padding: 8px;
              background-color: #fff;
              color: #333;
            }
          }

          /* Custom Arrow */
          .custom-select-container::after {
            content: "";
            position: absolute;
            top: 50%;
            transform: translate(-100%, -50%);
            right: 16px;
            pointer-events: none; /* Prevent clicking on the arrow */
            width: 16px; /* Width of the custom arrow */
            height: 16px; /* Height of the custom arrow */
            background-image: url('data:image/svg+xml,%3Csvg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M12 15L7 10H17L12 15Z" fill="%2340484C"/%3E%3C/svg%3E');
            background-size: contain;
            background-repeat: no-repeat;
          }
        }
      }
    }

    .qpe-modal-footer {
      display: flex;
      justify-content: flex-end;
      gap: 12px;
      padding: 24px;
      background: var(--surface-container-high);

      button {
        padding: 10px 12px;
        font-size: 14px;
        border: none;
        background: none;
        border-radius: 4px;
        cursor: pointer;
        color: var(--primary);
      }
    }
  }
}

@keyframes slideFromTopSmooth {
  0% {
    transform: translateY(-150%);
    opacity: 0;
  }
  60% {
    transform: translateY(10%);
    opacity: 1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
