.qpeLoader-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1;
  
  .qpeSpinner {
    width: 50px;
    height: 50px;
    border-top: 5px solid var(--dark-text);
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
