@use "sass:color";

.qpeMainButtonContainer {
  padding: 20px;
  background: var(--white);
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.participantIconContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 16px 10px 12px;
  color: var(--primary);
  gap: 8px;
}

.statusContainer {
  display: flex;
  gap: 8px;
  color: var(--primary);
  padding: 10px 16px 10px 12px;
  align-items: center;
}

.qpeSummaryButton {
  padding: 10px 24px;
  border: 1px solid var(--outline);
  border-radius: 100px;
  display: flex;
  gap: 8px;
  align-items: center;
  background: var(--white);
  justify-content: center;
  font-weight: bold;
  color: var(--primary);

  &.save {
    color: var(--white);
    background: var(--primary);
    padding: 10px 24px;
  }

  &.greenButton {
    padding: 10px 24px 10px 16px;
    background: var(--primary);
  }
}

.qpeButtonContainer {
  display: flex;
}

.qpeScribe-button {
  display: flex;
  color: var(--white);
  border: none;
  cursor: pointer;
  margin: auto;
  border-radius: 100px;
  padding: 8px;
  gap: 8px;
  background: var(--primary);
  align-items: center;
  justify-content: center;
  margin: auto;

  &.qpeScribe-disabled {
    background: none;
    color: var(--white);
  }

  &.normalButton {
    background: var(--secondary-color);
  }

  &.hangupIcon {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 24px 10px 16px;
    background: var(--error);
  }
}
